	module.exports = {
		"data": {
			"file": {
				"relativePath": "api/Int32.html",
				"childRawHtml": {
					"content": "<div class=\"ocamldoc\">\n<div class=\"navbar\"><a class=\"pre\" href=\"Hashtbl.html\" title=\"Hashtbl\">Previous</a>\n&#xA0;<a class=\"up\" href=\"index.html\" title=\"Index\">Up</a>\n&#xA0;<a class=\"post\" href=\"Int64.html\" title=\"Int64\">Next</a>\n</div>\n<h1>Module <a href=\"type_Int32.html\">Int32</a></h1>\n\n<pre><span class=\"keyword\">module</span> Int32: sig .. end</pre><div class=\"info module top\">\n32-bit integers.\n<p>\n\n   This module provides operations on the type <code class=\"code\">int32</code>\n   of signed 32-bit integers.  Unlike the built-in <code class=\"code\">int</code> type,\n   the type <code class=\"code\">int32</code> is guaranteed to be exactly 32-bit wide on all\n   platforms.  All arithmetic operations over <code class=\"code\">int32</code> are taken\n   modulo 2<sup class=\"superscript\">32</sup>.\n</p><p>\n\n   Performance notice: values of type <code class=\"code\">int32</code> occupy more memory\n   space than values of type <code class=\"code\">int</code>, and arithmetic operations on\n   <code class=\"code\">int32</code> are generally slower than those on <code class=\"code\">int</code>.  Use <code class=\"code\">int32</code>\n   only when the application requires exact 32-bit arithmetic.<br>\n</p></div>\n<hr width=\"100%\">\n\n<pre id=\"VALzero\">val zero : int32</pre><div class=\"info \">\nThe 32-bit integer 0.<br>\n</div>\n\n<pre id=\"VALone\">val one : int32</pre><div class=\"info \">\nThe 32-bit integer 1.<br>\n</div>\n\n<pre id=\"VALminus_one\">val minus_one : int32</pre><div class=\"info \">\nThe 32-bit integer -1.<br>\n</div>\n\n<pre id=\"VALneg\">val neg : int32 -&gt; int32</pre><div class=\"info \">\nUnary negation.<br>\n</div>\n\n<pre id=\"VALadd\">val add : int32 -&gt; int32 -&gt; int32</pre><div class=\"info \">\nAddition.<br>\n</div>\n\n<pre id=\"VALsub\">val sub : int32 -&gt; int32 -&gt; int32</pre><div class=\"info \">\nSubtraction.<br>\n</div>\n\n<pre id=\"VALmul\">val mul : int32 -&gt; int32 -&gt; int32</pre><div class=\"info \">\nMultiplication.<br>\n</div>\n\n<pre id=\"VALdiv\">val div : int32 -&gt; int32 -&gt; int32</pre><div class=\"info \">\nInteger division.  Raise <code class=\"code\">Division_by_zero</code> if the second\n   argument is zero.  This division rounds the real quotient of\n   its arguments towards zero, as specified for <a href=\"Pervasives.html#VAL(/)\"><code class=\"code\">Pervasives.(/)</code></a>.<br>\n</div>\n\n<pre id=\"VALrem\">val rem : int32 -&gt; int32 -&gt; int32</pre><div class=\"info \">\nInteger remainder.  If <code class=\"code\">y</code> is not zero, the result\n   of <code class=\"code\">Int32.rem x y</code> satisfies the following property:\n   <code class=\"code\">x = Int32.add (Int32.mul (Int32.div x y) y) (Int32.rem x y)</code>.\n   If <code class=\"code\">y = 0</code>, <code class=\"code\">Int32.rem x y</code> raises <code class=\"code\">Division_by_zero</code>.<br>\n</div>\n\n<pre id=\"VALsucc\">val succ : int32 -&gt; int32</pre><div class=\"info \">\nSuccessor.  <code class=\"code\">Int32.succ x</code> is <code class=\"code\">Int32.add x Int32.one</code>.<br>\n</div>\n\n<pre id=\"VALpred\">val pred : int32 -&gt; int32</pre><div class=\"info \">\nPredecessor.  <code class=\"code\">Int32.pred x</code> is <code class=\"code\">Int32.sub x Int32.one</code>.<br>\n</div>\n\n<pre id=\"VALabs\">val abs : int32 -&gt; int32</pre><div class=\"info \">\nReturn the absolute value of its argument.<br>\n</div>\n\n<pre id=\"VALmax_int\">val max_int : int32</pre><div class=\"info \">\nThe greatest representable 32-bit integer, 2<sup class=\"superscript\">31</sup> - 1.<br>\n</div>\n\n<pre id=\"VALmin_int\">val min_int : int32</pre><div class=\"info \">\nThe smallest representable 32-bit integer, -2<sup class=\"superscript\">31</sup>.<br>\n</div>\n\n<pre id=\"VALlogand\">val logand : int32 -&gt; int32 -&gt; int32</pre><div class=\"info \">\nBitwise logical and.<br>\n</div>\n\n<pre id=\"VALlogor\">val logor : int32 -&gt; int32 -&gt; int32</pre><div class=\"info \">\nBitwise logical or.<br>\n</div>\n\n<pre id=\"VALlogxor\">val logxor : int32 -&gt; int32 -&gt; int32</pre><div class=\"info \">\nBitwise logical exclusive or.<br>\n</div>\n\n<pre id=\"VALlognot\">val lognot : int32 -&gt; int32</pre><div class=\"info \">\nBitwise logical negation<br>\n</div>\n\n<pre id=\"VALshift_left\">val shift_left : int32 -&gt; int -&gt; int32</pre><div class=\"info \">\n<code class=\"code\">Int32.shift_left x y</code> shifts <code class=\"code\">x</code> to the left by <code class=\"code\">y</code> bits.\n   The result is unspecified if <code class=\"code\">y &lt; 0</code> or <code class=\"code\">y &gt;= 32</code>.<br>\n</div>\n\n<pre id=\"VALshift_right\">val shift_right : int32 -&gt; int -&gt; int32</pre><div class=\"info \">\n<code class=\"code\">Int32.shift_right x y</code> shifts <code class=\"code\">x</code> to the right by <code class=\"code\">y</code> bits.\n   This is an arithmetic shift: the sign bit of <code class=\"code\">x</code> is replicated\n   and inserted in the vacated bits.\n   The result is unspecified if <code class=\"code\">y &lt; 0</code> or <code class=\"code\">y &gt;= 32</code>.<br>\n</div>\n\n<pre id=\"VALshift_right_logical\">val shift_right_logical : int32 -&gt; int -&gt; int32</pre><div class=\"info \">\n<code class=\"code\">Int32.shift_right_logical x y</code> shifts <code class=\"code\">x</code> to the right by <code class=\"code\">y</code> bits.\n   This is a logical shift: zeroes are inserted in the vacated bits\n   regardless of the sign of <code class=\"code\">x</code>.\n   The result is unspecified if <code class=\"code\">y &lt; 0</code> or <code class=\"code\">y &gt;= 32</code>.<br>\n</div>\n\n<pre id=\"VALof_int\">val of_int : int -&gt; int32</pre><div class=\"info \">\nConvert the given integer (type <code class=\"code\">int</code>) to a 32-bit integer\n    (type <code class=\"code\">int32</code>).<br>\n</div>\n\n<pre id=\"VALto_int\">val to_int : int32 -&gt; int</pre><div class=\"info \">\nConvert the given 32-bit integer (type <code class=\"code\">int32</code>) to an\n   integer (type <code class=\"code\">int</code>).  On 32-bit platforms, the 32-bit integer\n   is taken modulo 2<sup class=\"superscript\">31</sup>, i.e. the high-order bit is lost\n   during the conversion.  On 64-bit platforms, the conversion\n   is exact.<br>\n</div>\n\n<pre id=\"VALof_float\">val of_float : float -&gt; int32</pre><div class=\"info \">\nConvert the given floating-point number to a 32-bit integer,\n   discarding the fractional part (truncate towards 0).\n   The result of the conversion is undefined if, after truncation,\n   the number is outside the range [<a href=\"Int32.html#VALmin_int\"><code class=\"code\">Int32.min_int</code></a>, <a href=\"Int32.html#VALmax_int\"><code class=\"code\">Int32.max_int</code></a>].<br>\n</div>\n\n<pre id=\"VALto_float\">val to_float : int32 -&gt; float</pre><div class=\"info \">\nConvert the given 32-bit integer to a floating-point number.<br>\n</div>\n\n<pre id=\"VALof_string\">val of_string : string -&gt; int32</pre><div class=\"info \">\nConvert the given string to a 32-bit integer.\n   The string is read in decimal (by default) or in hexadecimal,\n   octal or binary if the string begins with <code class=\"code\">0x</code>, <code class=\"code\">0o</code> or <code class=\"code\">0b</code>\n   respectively.\n   Raise <code class=\"code\">Failure &quot;int_of_string&quot;</code> if the given string is not\n   a valid representation of an integer, or if the integer represented\n   exceeds the range of integers representable in type <code class=\"code\">int32</code>.<br>\n</div>\n\n<pre id=\"VALto_string\">val to_string : int32 -&gt; string</pre><div class=\"info \">\nReturn the string representation of its argument, in signed decimal.<br>\n</div>\n\n<pre id=\"VALbits_of_float\">val bits_of_float : float -&gt; int32</pre><div class=\"info \">\nReturn the internal representation of the given float according\n   to the IEEE 754 floating-point &apos;single format&apos; bit layout.\n   Bit 31 of the result represents the sign of the float;\n   bits 30 to 23 represent the (biased) exponent; bits 22 to 0\n   represent the mantissa.<br>\n</div>\n\n<pre id=\"VALfloat_of_bits\">val float_of_bits : int32 -&gt; float</pre><div class=\"info \">\nReturn the floating-point number whose internal representation,\n   according to the IEEE 754 floating-point &apos;single format&apos; bit layout,\n   is the given <code class=\"code\">int32</code>.<br>\n</div>\n\n<pre id=\"TYPEt\"><span class=\"keyword\">type</span> t = int32 </pre>\n<div class=\"info \">\nAn alias for the type of 32-bit integers.<br>\n</div>\n\n\n<pre id=\"VALcompare\">val compare : t -&gt; t -&gt; int</pre><div class=\"info \">\nThe comparison function for 32-bit integers, with the same specification as\n    <a href=\"Pervasives.html#VALcompare\"><code class=\"code\">Pervasives.compare</code></a>.  Along with the type <code class=\"code\">t</code>, this function <code class=\"code\">compare</code>\n    allows the module <code class=\"code\">Int32</code> to be passed as argument to the functors\n    <a href=\"Set.Make.html\"><code class=\"code\">Set.Make</code></a> and <a href=\"Map.Make.html\"><code class=\"code\">Map.Make</code></a>.<br>\n</div>\n<br>\n<h6 id=\"6_Deprecatedfunctions\">Deprecated functions</h6><br>\n\n<pre id=\"VALformat\">val format : string -&gt; int32 -&gt; string</pre><div class=\"info \">\nDo not use this deprecated function.  Instead,\n   used <a href=\"Printf.html#VALsprintf\"><code class=\"code\">Printf.sprintf</code></a> with a <code class=\"code\">%l...</code> format.<br>\n</div>\n</div>"
				}
			}
		},
		"pathContext": {
			"relativePath": "api/Int32.html"
		}
	};

